.message-container {
  display: flex;
  align-items: flex-start;
  margin: 16px 0;
  animation: slideIn 0.3s ease-out;
  
  &.bot-message {
    flex-direction: row;
  }
}

.message-avatar {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  flex-shrink: 0;
  margin-right: 12px;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
  
  mat-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }
}

.message-bubble {
  max-width: 75%;
  padding: 12px 16px;
  border-radius: 12px 12px 12px 4px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.2);
  word-wrap: break-word;
  white-space: pre-wrap;
  line-height: 1.5;
}

.message-text {
  font-size: 14px;
}

.loading-dots {
  display: flex;
  gap: 6px;
  padding: 4px 0;
  
  span {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.8);
    animation: bounce 1.4s infinite ease-in-out;
    
    &:nth-child(1) {
      animation-delay: -0.32s;
    }
    
    &:nth-child(2) {
      animation-delay: -0.16s;
    }
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}